.menu {
  margin: 0;
  padding: 0;
  max-height: calc(100vh - 244px);
  overflow-y: auto;
  scrollbar-gutter: stable;
}

.menu::-webkit-scrollbar-track {
  margin-top: 40px;
	margin-bottom: 52px;
}

.menu::-webkit-scrollbar {
	width: 8px;
}

.menu::-webkit-scrollbar-thumb {
  border: none;
  border-radius: 0;
}